import React from 'react';

import 'styles/profile.css';
import User from "pages/LandingPages/Author/sections/User";
import MKBox from 'components/MKBox';
import DefaultNavbar from 'examples/Navbars/DefaultNavbar';
import routes from "routes";



const data = [
  { id: 1, nombre: "Luz Rodriguez", puesto:"Tutora de primaria", descripcion:"Mi enseanza hacia los alumnos es primordia", direccion:"Tomas Gonzalez #187", imagen:"https://i.postimg.cc/MGHWdV9y/team-3.jpg"},
  { id: 2, nombre: "Amanda Arriaga", puesto:"Maestra de Ingles", descripcion:"Manejo varios niveles para un progreso de los alumnos", direccion:"Colosio #46", imagen:"https://i.postimg.cc/1t61jHwN/team-1.jpg"},
];

class PerfilEducacion extends React.Component{
  constructor(props){
    super(props);

    this.state ={
      data:data
    }
  }

  render(){
    return (
        <>
          <DefaultNavbar
            routes={routes}
          /> 
          <MKBox
            minHeight="25vh"
            width="100%"
            sx={{
              
              backgroundSize: "cover",
              backgroundPosition: "top",
              display: "grid",
              placeItems: "center",
            }}
          />  
            {
            this.state.data.map((elemento)=>(
              <User 
              nombre ={elemento.nombre}
              puesto ={elemento.puesto}
              direccion ={elemento.direccion}
              imagen ={elemento.imagen}
              />
            ))
          }
        </>
      );
    }
  }
export default PerfilEducacion;