
// @mui material components
import Container from "@mui/material/Container";
import Grid from "@mui/material/Grid";
import { Link } from "react-router-dom";

import MKBox from "components/MKBox";
import MKAvatar from "components/MKAvatar";
import MKTypography from "components/MKTypography";
// import Profile1 from "./Profile1";
import Icon from "@mui/material/Icon";
 import profilePicture from "assets/images/bruce-mars.jpg";
// Images
import bgImage from "assets/images/examples/blog2.jpg";


function User({nombre, puesto, direccion, imagen}) {
  const imagenU=imagen;
  return (
    <MKBox component="section" py={{ xs: 0, lg: 6 }}>
      <Container>
      
        <Grid container item>
          <MKBox
            width="100%"
            bgColor="white"
            borderRadius="xl"
            shadow="xl"
            mb={6}
            sx={{ overflow: "hidden" }}
          >
            <Grid container spacing={2}>
              <Grid
                item
                xs={12}
                lg={5}
                position="relative"
                px={0}
                sx={{
                  backgroundImage: ({
                    palette: { gradients },
                    functions: { rgba, linearGradient },
                  }) =>
                    `${linearGradient(
                      rgba(gradients.dark.main, 0.8),
                      rgba(gradients.dark.state, 0.8)
                    )}, url(${bgImage})`,
                  backgroundSize: "cover",
                }}
              >
                <MKBox
                  display="flex"
                  justifyContent="center"
                  alignItems="center"
                  width="100%"
                  height="100%"
                >
                  <MKBox py={6} pr={6} pl={{ xs: 6, sm: 12 }} my="auto">
                  <MKAvatar src={imagenU} alt="Burce Mars" size="xxl" shadow="xl" />
                    <MKTypography variant="h3" color="light" mb={1}>
                      {nombre} 
                    </MKTypography>
                    <MKTypography variant="body1" color="warning" mb={1}>
                      {puesto} 
                    </MKTypography>
                    <MKTypography variant="body2" color="white" opacity={0.8} mb={1}>
                      Datos Verificados
                    </MKTypography>
                    <MKBox display="flex" color="white" p={1}>
                      <MKTypography variant="button" color="white">
                        <i className="fas fa-map-marker-alt" />
                      </MKTypography>
                      <MKTypography
                        component="span"
                        variant="button"
                        color="white"
                        opacity={0.8}
                        ml={1}
                        fontWeight="regular"
                      >
                        {direccion}
                      </MKTypography>
                    </MKBox>
                    <MKTypography
                        component={Link}
                        to="/pages/landing-pages/author" 
                        variant="body1"
                        fontWeight="light"
                        color="info"
                        mt={1}
                        sx={{
                          width: "max-content",
                          display: "flex",
                          alignItems: "center",
                          "& .material-icons-round": {
                            transform: `translateX(3px)`,
                            transition: "transform 0.2s cubic-bezier(0.34, 1.61, 0.7, 1.3)",
                          },
                          "&:hover .material-icons-round, &:focus .material-icons-round": {
                            transform: `translateX(6px)`,
                          },
                        }}
                      >
                        Ver Perfil <Icon sx={{ fontWeight: "bold" }}>arrow_forward</Icon>
                      </MKTypography>
                  </MKBox>
                </MKBox>
              </Grid>
            </Grid>
          </MKBox>
        </Grid>
      </Container>
    </MKBox>
  );
}

export default User;
 