

import { useState } from "react";

// react-router-dom components
import { Link } from "react-router-dom";

// @mui material components
import Card from "@mui/material/Card";
import Switch from "@mui/material/Switch";
import Grid from "@mui/material/Grid";

import MKBox from "components/MKBox";
import MKTypography from "components/MKTypography";
import MKInput from "components/MKInput";
import MKButton from "components/MKButton";

import logoImage from "assets/images/logoApo.jpg";
import Modal from '@mui/material/Modal';
import Box from '@mui/material/Box';




const style = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: 400,
  p: 4,
};

function SignIn() {
  const [rememberMe, setRememberMe] = useState(false);

  const handleSetRememberMe = () => setRememberMe(!rememberMe);

  const [open, setOpen] = useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);


  return (
    <>
      
      <MKBox
        position="absolute"
        top={0}
        left={0}
        zIndex={1}
        width="100%"
        minHeight="100vh"
        style={{backgroundColor:'#000000'}}
      />
      <MKBox px={1} width="100%" height="100vh" mx="auto" position="relative" zIndex={2}>
        <Grid container spacing={1} justifyContent="center" alignItems="center" height="100%">
          <Grid item xs={11} sm={9} md={5} lg={4} xl={3}>
            <Card >
              <MKBox sx={{
                backgroundImage: `url(${logoImage})`,
                backgroundSize: "cover",
                backgroundPosition: "center",
                backgroundRepeat: "no-repeat",
              }}
                style={{backgroundColor:'#F5900C'}}
                
                variant="gradient"
                borderRadius="lg"
                coloredShadow="info"
                mx={2}
                mt={-3}
                p={15}
                mb={1}
                textAlign="center"
              />
              
              
              <MKBox pt={4} pb={3} px={3}>
                <MKBox component="form" role="form">
                  <MKBox mb={2}>
                    <MKInput type="email" label="Email" fullWidth />
                  </MKBox>
                  <MKBox mb={2}>
                    <MKInput type="password" label="Password" fullWidth />
                  </MKBox>
                  <MKBox display="flex" alignItems="center" ml={-1}>
                    <Switch checked={rememberMe} onChange={handleSetRememberMe} />
                    <MKTypography
                      variant="button"
                      fontWeight="regular"
                      color="text"
                      onClick={handleSetRememberMe}
                      sx={{ cursor: "pointer", userSelect: "none", ml: -1 }}
                    >
                      &nbsp;&nbsp;Recurdame
                    </MKTypography>
                  </MKBox>
                  <MKBox mt={4} mb={1}>
                    <MKButton 
                    
                    style={{backgroundColor:'#0E3673', color:'white', marginBottom:'10px'}}
                    onClick={handleOpen}
                    variant="gradient" fullWidth>
                      Iniciar sesin
                    </MKButton>
                    
                    <MKButton 
                    style={{backgroundColor:'#F5900C', color:'white'}}
                    onClick={handleOpen}
                     variant="gradient" fullWidth>
                      Invitado
                    </MKButton>
                    <Modal
                                      open={open}
                                      onClose={handleClose}
                                      aria-labelledby="modal-modal-title"
                                      aria-describedby="modal-modal-description"
                                    >
                                      
                                    <Box sx={style}>
                                      <MKBox
                                          variant="gradient"
                                          coloredShadow="info"
                                          borderRadius="lg"
                                          p={2}
                                          mx={2}
                                          mt={-3}
                                          style={{ backgroundColor: 'white' }}
                                        >
                                          <Grid container item justifyContent="center" xs={12}>
                                          <MKTypography variant="h3" color="info" mb={1} ml={6}>
                                          Versin en desarrollo
                                          </MKTypography>
                                          <MKBox display="flex" color="white" p={1}>
                                            <MKTypography
                                              component="span"
                                              variant="button"
                                              color="black"
                                              opacity={0.8}
                                              ml={1}
                                              fontWeight="regular"
                                            >
                                              La app sigue en desarrollo, est versin solo se tendr visibilidad de las caractersticas
                                              escenciales que la componen
                                            </MKTypography>
                                          </MKBox>
                                          
                                            <MKButton 
                                            ml={5}
                                            style={{ backgroundColor: '#0E3673', variant: 'gradient', color: 'white' }}
                                            component={Link}
                                            to="/pages/landing-pages/about-us">
                                                Continuar
                                            </MKButton>
                                         </Grid>
                                        </MKBox>
                                    </Box>
                                      
                                    </Modal>

                  </MKBox>
                  <MKBox mt={3} mb={1} textAlign="center">
                    <MKTypography variant="button" color="text">
                      No tienes cuenta?{" "}
                      <MKTypography
                        component={Link}
                        to="/FormAccount"
                        variant="button"
                        color="info"
                        fontWeight="medium"
                        textGradient
                      >
                        Crear cuenta
                      </MKTypography>
                    </MKTypography>
                  </MKBox>
                </MKBox>
              </MKBox>
            </Card>
          </Grid>
        </Grid>
      </MKBox>
      
    </>
  );
}

export default SignIn;