
// @mui material components
import Container from "@mui/material/Container";
import Grid from "@mui/material/Grid";
import Icon from "@mui/material/Icon";
import { Link } from "react-router-dom";
// Material Kit 2 React components
import MKBox from "components/MKBox";
import MKAvatar from "components/MKAvatar";
// import MKButton from "components/MKButton";
import MKTypography from "components/MKTypography";

// Images
import profilePicture from "assets/images/bruce-mars.jpg";
import MKButton from "components/MKButton";

// nombre, descripcion, direccion, puesto, horarios
function Profile() {
  return (
    <MKBox component="section" py={{ xs: 6, sm: 12 }}>
      <Container>
        <Grid container item xs={12} justifyContent="center" mx="auto">
          <MKBox mt={{ xs: -16, md: -20 }} textAlign="center">
            <MKAvatar src={profilePicture} alt="Burce Mars" size="xxl" shadow="xl" />
          </MKBox>
          <Grid container justifyContent="center" py={6}>
            <Grid item xs={12} md={7} mx={{ xs: "auto", sm: 6, md: 1 }}>
              <MKBox display="flex" justifyContent="space-between" alignItems="center" mb={1}>
                <MKTypography variant="h3">Martin Guzmn</MKTypography>
              </MKBox>
              <Grid container spacing={3} mb={3}>
                <Grid item>
                  <MKTypography component="span" variant="body2" fontWeight="bold">
                    20&nbsp;
                  </MKTypography>
                  <MKTypography component="span" variant="body2" color="text">
                    Trabajos
                  </MKTypography>
                </Grid>
                <Grid item>
                  <MKTypography component="span" variant="body2" fontWeight="bold">
                    3.5&nbsp;
                  </MKTypography>
                  <MKTypography component="span" variant="body2" color="text">
                    Calificacin
                  </MKTypography>
                </Grid>
                <Grid item>
                  <MKButton 
                  component={Link}
                  to="/pages/landing-pages/pay"
                  variant="gradient" color="warning">
                    Contratar
                  </MKButton>
                </Grid>
              </Grid>
              <MKTypography variant="body1" color="warning" mb={1}>
                Organizador de Fiestas
              </MKTypography>
              <MKTypography variant="body2" color="white" opacity={0.8} mb={1}>
                      Horarios: 8:00 a.m. - 8:00 p.m.
              </MKTypography>
              <MKTypography variant="body1" fontWeight="light" color="text">
              Tengo excelentes ideas para los jovenes, VX y Bodas con temticas distintas
                <MKTypography
                  component={Link}
                  to="/contact-us/contact-them"
                  variant="body1"
                  fontWeight="light"
                  color="info"
                  mt={3}
                  sx={{
                    width: "max-content",
                    display: "flex",
                    alignItems: "center",

                    "& .material-icons-round": {
                      transform: `translateX(3px)`,
                      transition: "transform 0.2s cubic-bezier(0.34, 1.61, 0.7, 1.3)",
                    },

                    "&:hover .material-icons-round, &:focus .material-icons-round": {
                      transform: `translateX(6px)`,
                    },
                  }}
                >
                  Contactarme <Icon sx={{ fontWeight: "bold" }}>arrow_forward</Icon>
                </MKTypography>
              </MKTypography>
            </Grid>
          </Grid>
        </Grid>
      </Container>
    </MKBox>
  );
}

export default Profile;
