

/** 
  All of the routes for the Material Kit 2 React React are added here,
  You can add a new route, customize the routes and delete the routes here.

  Once you add a new route on this file it will be visible automatically on
  the Navbar.

  For adding a new route you can follow the existing routes in the routes array.
  1. The `name` key is used for the name of the route on the Navbar.
  2. The `icon` key is used for the icon of the route on the Navbar.
  3. The `collapse` key is used for making a collapsible item on the Navbar that contains other routes
  inside (nested routes), you need to pass the nested routes inside an array as a value for the `collapse` key.
  4. The `route` key is used to store the route location which is used for the react router.
  5. The `href` key is used to store the external links location.
  6. The `component` key is used to store the component of its route.
  7. The `dropdown` key is used to define that the item should open a dropdown for its collapse items .
  8. The `description` key is used to define the description of 
          a route under its name.
  9. The `columns` key is used to define that how the content should look inside the dropdown menu as columns,
          you can set the columns amount based on this key.
  10. The `rowsPerColumn` key is used to define that how many rows should be in a column.
*/

// @mui material components
import Icon from "@mui/material/Icon";

// @mui icons
// import GitHubIcon from "@mui/icons-material/GitHub";

// Pages
import AboutUs from "layouts/pages/landing-pages/about-us";
import ContactUs from "layouts/pages/landing-pages/contact-us";
import Author from "layouts/pages/landing-pages/author";
import SignIn from "layouts/pages/authentication/sign-in";
import Home from "layouts/pages/landing-pages/home";
import PerfilBelleza from "pages/LandingPages/Perfiles/Belleza";
import PerfilEducacion from "pages/LandingPages/Perfiles/Educacion";
import PerfilEventos from "pages/LandingPages/Perfiles/Eventos";
import PerfilHogar from "pages/LandingPages/Perfiles/Hogar";
import PerfilOtros from "pages/LandingPages/Perfiles/Otros";
import PerfilSalud from "pages/LandingPages/Perfiles/Salud";
import FormAccount from "pages/LandingPages/FormAccount";
import CreditCards from "pages/LandingPages/Pay";

import RegistroTrabajador from "pages/LandingPages/RegistroTrabajador";
import Terminos from "pages/LandingPages/Terminos";
import ContactThem from "pages/LandingPages/ContactUs/sections/ContactThem";
// import Loading from "pages/LandingPages/Loading";

const routes = [
    {
    name: "Perfiles",
        collapse:[
          {
            name: "belleza",
            route: "/perfiles/belleza",
            component: <PerfilBelleza />
          },
          {
            name: "educacion",
            route: "/perfiles/educacion",
            component: <PerfilEducacion />
          },{
            name: "eventos",
            route: "/perfiles/eventos",
            component: <PerfilEventos />
          },{
            name: "hogar",
            route: "/perfiles/hogar",
            component: <PerfilHogar />
          },{
            name: "otros",
            route: "/perfiles/otros",
            component: <PerfilOtros />
          },{
            name: "salud",
            route: "/perfiles/salud",
            component: <PerfilSalud />
          },
        ],
      },
      {
        name: "Pginas",
        icon: <Icon>dashboard</Icon>,
        columns: 1,
        rowsPerColumn: 2,
            collapse: [
              {
                name: "Sobre Nosotros",
                route: "/pages/landing-pages/about-us",
                component: <AboutUs/>,
              },
              {
                name: "Contactarnos",
                route: "/contact-us",
                component: <ContactUs />,
              },
              {
                name: "Contactarlo",
                route: "/contact-us/contact-them",
                component: <ContactThem />,
              },
              {
                name: "Usuario",
                route: "/pages/landing-pages/author",
                component: <Author />,
              },
              {
                name: "Inicio",
                route: "/home",
                component: <Home />,
              },
              {
                name: "Hacer Pagos",
                route: "/pages/landing-pages/pay",
                component: <CreditCards/>,
              },
              {
                name: "Registro de Trabajo",
                route: "/pages/landing-pages/registro-trabajador",
                component: <RegistroTrabajador/>,
                
              },
              {
                name: "Trminos y Condicines",
                route: "/pages/landing-pages/terminos",
                component: <Terminos/>,
                
              },
            ],
          },    
      {
        name: "Cuenta",
        icon: <Icon>account_box</Icon>,
            collapse: [
              {
                name: "Iniciar Sesin",
                route: "/pages/authentication/sign-in",
                component: <SignIn />,
              },
              {
                name: "Cerrar Sesin",
                route: "/pages/authentication/sign-in",
                component: <SignIn />,
              },
              {
                name: "Crear Cuenta",
                route: "/FormAccount",
                component: <FormAccount />,
              },

            ],
      },
];

export default routes;
