import Grid from "@mui/material/Grid";

// Material Kit 2 React components
import MKBox from "components/MKBox";
import MKInput from "components/MKInput";
import MKButton from "components/MKButton";
import MKTypography from "components/MKTypography";
import { useState } from "react";
import { Link } from "react-router-dom";
// import Checkbox from "@material-ui/core/Checkbox/Checkbox";
// import { FormControlLabel } from "@mui/material";


function FormAccount() {
  const [data, setData] = useState({
    name: "",
    age: "",
    email: "",
    password: ""

  })
    const handleChange = (e) => {
      setData({ ...data, [e.target.name]: e.target.value });
    }
    const submitForm = (e) => {
      e.preventDefault();
      /* eslint-disable */ 
      const sendData = {
        name: data.name,
        age: data.age,
        email: data.email,
        password: data.password

      }
      
    }
    const [open, setOpen] = useState(false);
    const handleOpen = () => setOpen(true);
    const handleClose = () => setOpen(false);
    const style = {
      position: 'absolute',
      top: '50%',
      left: '50%',
      transform: 'translate(-50%, -50%)',
      width: 400,
      p: 4,
    };

  return (
    <>
      <form onSubmit={submitForm}>
        <Grid container spacing={3} alignItems="center">
          <Grid item xs={12} lg={6}>
            <MKBox
              display={{ xs: "none", lg: "flex" }}
              width="calc(100% - 2rem)"
              height="calc(100vh - 2rem)"
              borderRadius="lg"
              ml={2}
              mt={2}
            />
          </Grid>
          <Grid
            item
            xs={12}
            sm={10}
            md={7}
            lg={6}
            xl={4}
            ml={{ xs: "auto", lg: 6 }}
            mr={{ xs: "auto", lg: 6 }}
          >
            <MKBox
              bgColor="white"
              borderRadius="xl"
              shadow="lg"
              display="flex"
              flexDirection="column"
              justifyContent="center"
              mt={{ xs: 20, sm: 18, md: 20 }}
              mb={{ xs: 20, sm: 18, md: 20 }}
              mx={3}

            >
              <MKBox
                variant="gradient"
                coloredShadow="info"
                borderRadius="lg"
                p={2}
                mx={2}
                mt={-3}
                style={{ backgroundColor: '#F5900C' }}

              >
                <MKTypography variant="h3" color="white">
                  Crear Cuenta
                </MKTypography>
              </MKBox>
              <MKBox p={3}>
                <MKTypography variant="body2" color="text" mb={3}>
                  Ingresa los datos para poder acceder a nuestro contenido y unirte a la comunidad APO Services
                </MKTypography>
                <MKBox width="100%" component="form" method="post" autocomplete="off">
                  <Grid container spacing={3}>
                    <Grid item xs={12} md={6}>
                      <MKInput
                        variant="standard"
                        label="Nombre Completo"
                        InputLabelProps={{ shrink: true }}
                        fullWidth
                        name="name"
                        onChange={handleChange} value={data.name}
                      />
                    </Grid>
                    <Grid item xs={5} md={6}>
                      <MKInput
                        variant="standard"
                        label="Edad"
                        InputLabelProps={{ shrink: true }}
                        fullWidth
                        name="age"
                        onChange={handleChange} value={data.age}
                      />

                    </Grid>
                    <Grid item xs={12} md={6}>
                      <MKInput
                        variant="standard"
                        label="Domicilio"
                        InputLabelProps={{ shrink: true }}
                        fullWidth
                      />
                    </Grid>
                    <Grid item xs={8} md={6}>
                      <MKInput
                        variant="standard"
                        label="Numero de Telefono"
                        InputLabelProps={{ shrink: true }}
                        fullWidth
                      />
                    </Grid>
                    <Grid item xs={12} md={6}>
                      <MKInput
                        type="email"
                        variant="standard"
                        label="Correo Electrnico"
                        InputLabelProps={{ shrink: true }}
                        fullWidth
                        name="email"
                        onChange={handleChange} value={data.email}
                      />
                    </Grid>
                    <Grid item xs={8} md={6}>
                      <MKInput
                        type="password"
                        variant="standard"
                        label="Contrasea"
                        InputLabelProps={{ shrink: true }}
                        fullWidth
                        name="password"
                        onChange={handleChange} value={data.password}
                      />
                    </Grid>
                    <Grid item xs={8} md={6}>
                      <MKInput
                        type="password"
                        variant="standard"
                        label="Repetir Contrasea"
                        InputLabelProps={{ shrink: true }}
                        fullWidth
                      />
                      
                    </Grid>
                    
                  </Grid>
                 
                      <MKTypography
                        component={Link}
                        to="/pages/landing-pages/terminos"
                        variant="button"
                        color="info"
                        fontWeight="medium"
                        textGradient
                      >
                        Trminos y Condicines
                      </MKTypography>
                  <Grid container item justifyContent="center" xs={12} mt={5} mb={2}>
                    <MKButton type="submit" name="submit" style={{ backgroundColor: '#0E3673', variant: 'gradient', color: 'white', marginBottom: '10px' }}
                    component={Link}
                    to="/home">
                      Registrarse
                    </MKButton>
                    
                  </Grid>
                </MKBox>
              </MKBox>
            </MKBox>
          </Grid>
        </Grid>
      </form>
    </>
  );
}

export default FormAccount;