
import { React,useState } from "react";
import Cards from "react-credit-cards";
import "react-credit-cards/es/styles-compiled.css";
import { Link } from "react-router-dom";
import MKBox from "components/MKBox";
import MKButton from "components/MKButton";
// import Card from "@mui/material/Card";
// import Switch from "@mui/material/Switch";
import Grid from "@mui/material/Grid";
import MKTypography from "components/MKTypography";
import MKInput from "components/MKInput";
import Modal from '@mui/material/Modal';
import Box from '@mui/material/Box';




const style = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: 400,
  p: 4,
};

const CreditCards = () =>{

  const [state,setState] = useState({
    number:"",
    name:"",
    expiry:"",
    cvc:"",
    focus:""
  })

  const handleInputChange =(e)=>{
    setState({
      ...state,
      [e.target.name]:e.target.value
    })
  }

  const handleFocusChange =(e)=>{
    setState({
      ...state,
      focus:e.target.name
    })
  }

  // const [data, setData] = useState({
  //   name: "",
  //   age: "",
  //   email: "",
  //   password: ""

  // })
    // const handleChange = (e) => {
    //   setData({ ...data, [e.target.name]: e.target.value });
    // }
    // const submitForm = (e) => {
    //   e.preventDefault();
    //   /* eslint-disable */
    //   const sendData = {
    //     name: data.name,
    //     age: data.age,
    //     email: data.email,
    //     password: data.password

    //   }

    // }
  // const processPayment = () => {
  //   console.log("number => ", state.number)
  //   console.log("name => ", state.name)
  //   console.log("expiry => ", state.expiry)
  //   console.log("cvc => ", state.cvc)
  //   console.log(JSON.stringify(state))

  // }



    const [open, setOpen] = useState(false);
    const handleOpen = () => setOpen(true);
    const handleClose = () => setOpen(false);


  return(
    <>

    <MKBox px={1} width="100%" height="100vh" mx="auto" position="relative" zIndex={2}>
      <Grid container spacing={1} justifyContent="center" alignItems="center" height="100%">
        <Grid item xs={11} sm={9} md={5} lg={4} xl={3}>


          <Grid container spacing={3} alignItems="center">
                <Grid item xs={12} lg={6}>
                  <MKBox
                    display={{ xs: "none", lg: "flex" }}
                    width="calc(100% - 2rem)"
                    height="calc(100vh - 2rem)"
                    borderRadius="lg"
                    ml={2}
                    mt={2}
                  />
                </Grid>
                <Grid
                  item
                  xs={12}
                  sm={10}
                  md={7}
                  lg={6}
                  xl={4}
                  ml={{ xs: "auto", lg: 6 }}
                  mr={{ xs: "auto", lg: 6 }}
                >
                  <MKBox
                    bgColor="white"
                    borderRadius="xl"
                    shadow="lg"
                    display="flex"
                    flexDirection="column"
                    justifyContent="center"
                    mt={{ xs: 20, sm: 18, md: 20 }}
                    mb={{ xs: 20, sm: 18, md: 20 }}
                    mx={3}

                  >
                    <MKBox
                      variant="gradient"
                      coloredShadow="info"
                      borderRadius="lg"
                      p={2}
                      mx={2}
                      mt={-3}
                      style={{ backgroundColor: '#F5900C' }}

                    >
                      <MKTypography variant="h3" color="white">
                        Contratar Servicios
                      </MKTypography>
                    </MKBox>
                    <MKBox p={3}>
                      <MKTypography variant="body2" color="text" mb={3}>
                        Proporciona los datos siguientes
                      </MKTypography>
                      <MKBox width="100%" component="form" autocomplete="off">
                      <div className="form-group" >
                              <MKBox pl={1} pr={1} pb={2}>
                                <label htmlFor="name"/>
                                <MKInput
                                  type="text"
                                  name="name_user"
                                  id="name_user"
                                  maxLength="16"
                                  className="form-control"
                                  label="Nombre Completo"
                                  fullWidth/>
                              </MKBox>
                              <MKBox pl={1} pr={1} pb={2}>
                                <MKInput
                                  type="text"
                                  name="address"
                                  id="address"
                                  maxLength="30"
                                  className="form-control"
                                  label="Domicilio"
                                  fullWidth/>
                              </MKBox>
                              <MKBox pl={1} pr={1} pb={2}>
                                <MKInput
                                type="text"
                                name="date_time"
                                id="date_time"
                                maxLength="4"
                                className="form-control"
                                label="Horario de Disponibilidad"
                                fullWidth/>
                              </MKBox>
                              <MKBox pl={1} pr={1}>
                                <MKInput
                                    type="text"
                                    name="detail_service"
                                    id="detail_service"
                                    maxLength="4"
                                    className="form-control"
                                    label="Servicio Requerido"
                                    fullWidth/>
                              </MKBox>

                              </div>
                        <div className="card-light">
                          <div className="card-body">
                            <MKBox p={1} pt={4}>
                              <Cards
                                number={state.number}
                                name={state.name}
                                expiry={state.expiry}
                                cvc={state.cvc}
                                focused={state.focus}
                              />
                            </MKBox>

                            {/* <form onSubmit={submitForm}> */}
                              <div className="form-group" >
                              <MKBox p={2}>
                                <label htmlFor="name"/>
                                <MKInput
                                  type="text"
                                  name="number"
                                  id="number"
                                  maxLength="16"
                                  className="form-control"
                                  onChange={handleInputChange}
                                  onFocus={handleFocusChange}
                                  label="Nmero de la Tarjeta"
                                  fullWidth/>
                              </MKBox>
                              <MKBox pl={2} pr={2}>
                                <MKInput
                                  type="text"
                                  name="name"
                                  id="name"
                                  maxLength="30"
                                  className="form-control"
                                  onChange={handleInputChange}
                                  onFocus={handleFocusChange}
                                  label="Nombre del titular"
                                  fullWidth/>
                              </MKBox>
                              <MKBox p={2}>
                                <MKInput
                                type="text"
                                name="expiry"
                                id="expiry"
                                maxLength="4"
                                className="form-control"
                                onChange={handleInputChange}
                                onFocus={handleFocusChange}
                                label="Fecha de Expiracin"
                                />
                              </MKBox>
                              <MKBox pl={2} pb={2}>
                                <MKInput
                                    type="password"
                                    name="cvc"
                                    id="cvc"
                                    maxLength="4"
                                    className="form-control"
                                    onChange={handleInputChange}
                                    onFocus={handleFocusChange} label="CVC" />
                              </MKBox>

                              </div>

                              <MKBox p={4}>
                                <Grid container item justifyContent="center" xs={12}>
                                    <MKButton style={{ backgroundColor: '#0E3673', variant: 'gradient', color: 'white' }}
                                    onClick={handleOpen}
                                    >
                                    Contratar
                                  </MKButton>
                                    <Modal
                                      open={open}
                                      onClose={handleClose}
                                      aria-labelledby="modal-modal-title"
                                      aria-describedby="modal-modal-description"
                                    >
                                      
                                    <Box sx={style}>
                                      <MKBox
                                          variant="gradient"
                                          coloredShadow="info"
                                          borderRadius="lg"
                                          p={2}
                                          mx={2}
                                          mt={-3}
                                          style={{ backgroundColor: 'white' }}
                                        >
                                          <Grid container item justifyContent="center" xs={12}>
                                          <MKTypography variant="h3" color="success" mb={1} ml={6}>
                                          PAGO FUE REALIZADO
                                          </MKTypography>
                                          <MKBox display="flex" color="white" p={1}>
                                            <MKTypography
                                              component="span"
                                              variant="button"
                                              color="black"
                                              opacity={0.8}
                                              ml={1}
                                              fontWeight="regular"
                                            >
                                              La transeferencia ser completada hasta que el usuario haya terminado con su trabajo
                                            </MKTypography>
                                          </MKBox>
                                          
                                            <MKButton 
                                            ml={5}
                                            style={{ backgroundColor: '#22bb33', variant: 'gradient', color: 'white' }}
                                                component={Link}
                                                to="/home">
                                                Continuar
                                            </MKButton>
                                         </Grid>
                                        </MKBox>
                                    </Box>
                                      
                                    </Modal>

                                </Grid>
                              </MKBox>


                          </div>
                        </div>
                      </MKBox>
                    </MKBox>
                  </MKBox>
                </Grid>
              </Grid>
        </Grid>
      </Grid>
    </MKBox>
</>
  )

}

export default  CreditCards;

