import React from 'react';

import 'styles/profile.css';
import User from "pages/LandingPages/Author/sections/User";
import MKBox from 'components/MKBox';
import DefaultNavbar from 'examples/Navbars/DefaultNavbar';
import routes from "routes";


const data = [
  { id: 1, nombre: "Jaime Domnguez", puesto:"Arquitecto", descripcion:"Tu creatividad no ser un problema mio", direccion:"Maximino Pozo #89",imagen:"https://i.postimg.cc/kM00h4NR/man-with-blueprints-and-helmet.jpg"},
  { id: 2, nombre: "Yulisa Gmez", puesto:"Abogada" , descripcion:"Especializada en Derecho Penal", direccion:"Gomez Morin #78",imagen:"https://i.postimg.cc/P5mF7QXB/side-view-woman-holding-ice-cream-cone.jpg"},
  
];

class PerfilOtros extends React.Component{
  constructor(props){
    super(props);

    this.state ={
      data:data
    }
  }

  render(){
    return (
        <>
          <DefaultNavbar
            routes={routes}
          /> 
          <MKBox
            minHeight="25vh"
            width="100%"
            sx={{
              
              backgroundSize: "cover",
              backgroundPosition: "top",
              display: "grid",
              placeItems: "center",
            }}
          />  
            {
            this.state.data.map((elemento)=>(
              <User 
              nombre ={elemento.nombre}
              puesto ={elemento.puesto}
              direccion ={elemento.direccion}
              imagen={elemento.imagen}
              />
            ))
          }
        </>
      );
      }
    }

export default PerfilOtros;