import React from 'react';

import 'styles/profile.css';
import User from "pages/LandingPages/Author/sections/User";
import MKBox from 'components/MKBox';
import DefaultNavbar from 'examples/Navbars/DefaultNavbar';
import routes from "routes";


const data = [
  { id: 1, nombre: "Patricia Martnez", puesto:"Cocinera", descripcion:"Cualquier platillo mexicano lo hago con ganas", direccion:"Mora #432",imagen:"https://i.postimg.cc/pLTyypg1/medium-shot-woman-wearing-bucket-hat.jpg"},
  { id: 2, nombre: "Pepe de Jess", puesto:"Electricista", descripcion:"Mi trabajo tiene garantia a largo plazo", direccion:"Matamoros #52",imagen:"https://i.postimg.cc/3RBHgZGj/portrait-of-smiling-young-man-in-eyewear.jpg"},
  { id: 3, nombre: "Angel Gutirrez", puesto:"Carpintero", descripcion:"Manejo excelentes trabajos con manera de excelente calidad", direccion:"Gonzalez Gallo #46",imagen:"https://i.postimg.cc/Bb79zQMX/portrait-of-young-asian-man-looking-confident-and-taking-selfie-while-standing-outdoors-in-the-stree.jpg"},
];

class PerfilHogar extends React.Component{
  constructor(props){
    super(props);

    this.state ={
      data:data
    }
  }
  
  render(){
    return (
        <>
          <DefaultNavbar
            routes={routes}
          /> 
          <MKBox
            minHeight="25vh"
            width="100%"
            sx={{
              
              backgroundSize: "cover",
              backgroundPosition: "top",
              display: "grid",
              placeItems: "center",
            }}
          />  
           {
            this.state.data.map((elemento)=>(
              <User 
              nombre ={elemento.nombre}
              puesto ={elemento.puesto}
              direccion ={elemento.direccion}
              imagen={elemento.imagen}
              />
            ))
          }
        </>
      );
    }
  }
export default PerfilHogar;