// import Container from "@mui/material/Container";
// import Grid from "@mui/material/Grid";
// import Card from "@mui/material/Card";
import React from 'react';
 import MKBox from "components/MKBox";
// import MKButton from "components/MKButton";
import DefaultNavbar from "examples/Navbars/DefaultNavbar";


import routes from "routes";
import User from "pages/LandingPages/Author/sections/User";

// Images


const data = [
  { id: 1, nombre: "Alan Gonzalez", puesto:"Barbero", descripcion:"Soy una persona confiable, veloz y disponibilidad a todas horas", direccion:"Porfirio diaz #271", imagen:"https://i.postimg.cc/zXT3gCQv/team-2.jpg"},
  { id: 2, nombre: "Pedro Sanchez", puesto:"Estilista", descripcion:"Soy bueno en mi trabajo con bajos precios", direccion:"Independencia #52A",imagen:"https://i.postimg.cc/6q2ttXkT/team-4.jpg"},
  { id: 3, nombre: "Fernanda Jimenez", puesto:"Ponedor de Uas", descripcion:"Tengo excelentes diseos y soy creativa en mi trabajo", direccion:"Gonzalez Gallo #454",imagen:"https://i.postimg.cc/fbJHHRm3/team-5.jpg"},
];

class PerfilBelleza extends React.Component {
  constructor(props){
    super(props);

    this.state ={
      data:data
    }
  }
  
  render(){
    return (
      <>
        <DefaultNavbar
          routes={routes}
        /> 
        <MKBox
          minHeight="25vh"
          width="100%"
          sx={{
            
            backgroundSize: "cover",
            backgroundPosition: "top",
            display: "grid",
            placeItems: "center",
          }}
        />  
          {
            this.state.data.map((elemento)=>(
              <User 
              nombre ={elemento.nombre}
              puesto ={elemento.puesto}
              direccion ={elemento.direccion}
              imagen ={elemento.imagen}
              />
            ))
          }
          
      </>
    );
  }
}

export default PerfilBelleza;

