import React from 'react';

import 'styles/profile.css';
import User from "pages/LandingPages/Author/sections/User";
import MKBox from 'components/MKBox';
import DefaultNavbar from 'examples/Navbars/DefaultNavbar';
import routes from "routes";

const data = [
  { id: 1, nombre: "Alejandro Solano", puesto:"Medico Personal", descripcion:"Me importa la el cuidado de las personas", direccion:"Donato Guerra #27",imagen:"https://i.postimg.cc/YSTRq4C1/portrait-of-successful-mid-adult-doctor-with-crossed-arms.jpg"},
  { id: 2, nombre: "Saul Guzman", puesto:"Nutriologo", descripcion:"Los resultados los vers en cuestin de das", direccion:"Benito Juarez #2A",imagen:"https://i.postimg.cc/jqPg2hNC/smiling-male-happy-doctor-offering-apple-and-broccoli.jpg"},
  { id: 3, nombre: "Alejandra Perez", puesto:"Fisioterapeuta", descripcion:"El cuidado de tu cuerpo es mi responsabilidad con cuidados de alta disponibilidad", direccion:"Guayaba #123",imagen:"https://i.postimg.cc/k5H161fR/sport-wellbeing-and-active-lifestyle-concept-amused-and-wondered-cute-slim-asian-fitness-girl-in-act.jpg"},
];

class PerfilSalud extends React.Component{
  constructor(props){
    super(props);

    this.state ={
      data:data
    }
  }

  render(){
    return (
        <>
          <DefaultNavbar
            routes={routes}
          /> 
          <MKBox
            minHeight="25vh"
            width="100%"
            sx={{
              
              backgroundSize: "cover",
              backgroundPosition: "top",
              display: "grid",
              placeItems: "center",
            }}
          />  
            {
            this.state.data.map((elemento)=>(
              <User 
              nombre ={elemento.nombre}
              puesto ={elemento.puesto}
              direccion ={elemento.direccion}
              imagen={elemento.imagen}
              />
            ))
          }
        </>
      );
    }
  }


export default PerfilSalud;