import React, {Fragment, useState} from 'react';
import Grid from "@mui/material/Grid";

// Material Kit 2 React components
import MKBox from "components/MKBox";
import MKInput from "components/MKInput";
import MKButton from "components/MKButton";
import MKTypography from "components/MKTypography";
import { Link } from "react-router-dom";
import Modal from '@mui/material/Modal';
import Box from '@mui/material/Box';

function RegistroTrabajador() {

  const [file, setFile] = useState(null)

  const selectedHandler = e => {
    setFile(e.target.files[0])
  }
  
  const [data, setData] = useState({
    name: "",
    age: "",
    email: "",
    password: ""

  })

    const handleChange = (e) => {
      setData({ ...data, [e.target.name]: e.target.value });
    }

    const submitForm = (e) => {
      e.preventDefault();
      /* eslint-disable */ 
      const sendData = {
        name: data.name,
        age: data.age,
        email: data.email,
        password: data.password

      }
      
    }

    const style = {
      position: 'absolute',
      top: '50%',
      left: '50%',
      transform: 'translate(-50%, -50%)',
      width: 400,
      p: 4,
    };
    
    const [open, setOpen] = useState(false);
    const handleOpen = () => setOpen(true);
    const handleClose = () => setOpen(false);

  const sendHandler = () => {
    if(!file){
      alert('you must upload file')
      return
    }

    const formdata = new FormData()
    formdata.append('image', file)

    fetch('http://localhost:9000/images/post', {
      method: 'POST',
      body: formdata
    })
    .then(res => res.text())
    .then(res => console.log(res))
    .catch(err => {
      console.error(err)
    })

    document.getElementById('fileinput').value = null

    setFile(null)
  }

  return (
    
    <Fragment>
      <form onSubmit={submitForm}>
        <Grid container spacing={3} alignItems="center">
          <Grid item xs={12} lg={6}>
            <MKBox
              display={{ xs: "none", lg: "flex" }}
              width="calc(100% - 2rem)"
              height="calc(100vh - 2rem)"
              borderRadius="lg"
              ml={2}
              mt={2}
            />
          </Grid>
          <Grid
            item
            xs={12}
            sm={10}
            md={7}
            lg={6}
            xl={4}
            ml={{ xs: "auto", lg: 6 }}
            mr={{ xs: "auto", lg: 6 }}
          >
            <MKBox
              bgColor="white"
              borderRadius="xl"
              shadow="lg"
              display="flex"
              flexDirection="column"
              justifyContent="center"
              mt={{ xs: 20, sm: 18, md: 20 }}
              mb={{ xs: 20, sm: 18, md: 20 }}
              mx={3}

            >
              <MKBox
                variant="gradient"
                coloredShadow="info"
                borderRadius="lg"
                p={2}
                mx={2}
                mt={-3}
                style={{ backgroundColor: '#F5900C' }}

              >
                <MKTypography variant="h3" color="white">
                  Registro de Trabajador
                </MKTypography>
              </MKBox>
              <MKBox p={3}>
                <MKTypography variant="body2" color="text" mb={3}>
                  Ingresa los datos para poder ofrecer tus servicios mediante nuestra paltaforma APO Services.
                  
                </MKTypography>
                <MKBox width="100%" component="form" method="post" autocomplete="off">
                  <Grid container spacing={3}>
                  <Grid item xs={12} md={6}>
                      <MKInput
                        variant="standard"
                        label="Foto de INE"
                        InputLabelProps={{ shrink: true }}
                        fullWidth
                        id="fileinput" 
                        onChange={selectedHandler} 
                        type="file"
                      />
                    </Grid>
                    <Grid item xs={12} md={6}>
                      <MKInput
                        variant="standard"
                        label="Nombre Completo"
                        InputLabelProps={{ shrink: true }}
                        fullWidth
                        name="name"
                        onChange={handleChange} value={data.name}
                      />
                    </Grid>
                    <Grid item xs={12} md={6}>
                      <MKInput
                        variant="standard"
                        label="Domicilio"
                        InputLabelProps={{ shrink: true }}
                        fullWidth
                      />
                    </Grid>
                    <Grid item xs={8} md={6}>
                      <MKInput
                        variant="standard"
                        label="Numero de Telefono"
                        InputLabelProps={{ shrink: true }}
                        fullWidth
                      />
                    </Grid>
                    <Grid item xs={12} md={6}>
                      <MKInput
                        type="email"
                        variant="standard"
                        label="Correo Electrnico"
                        InputLabelProps={{ shrink: true }}
                        fullWidth
                        name="email"
                        onChange={handleChange} value={data.email}
                      />
                    </Grid>
                    <Grid item xs={8} md={6}>
                      <MKInput
                        type="text"
                        variant="standard"
                        label="CURP"
                        InputLabelProps={{ shrink: true }}
                        fullWidth
                        name="password"
                        onChange={handleChange} value={data.password}
                      />
                    </Grid>
                    <Grid item xs={8} md={6}>
                      <MKInput
                        type="text"
                        variant="standard"
                        label="RFC"
                        InputLabelProps={{ shrink: true }}
                        fullWidth
                      />
                    </Grid>
                    <Grid item xs={8} md={6}>
                      <MKInput
                        type="text"
                        variant="standard"
                        label="Nmero de Cuenta Bancaria"
                        InputLabelProps={{ shrink: true }}
                        fullWidth
                      />
                    </Grid>

                  </Grid>
                  <Grid container item justifyContent="center" xs={12} mt={5} mb={2}>
                                  <MKButton    style={{ backgroundColor: '#0E3673', variant: 'gradient', color: 'white', marginBottom: '10px', marginRight:'10px' }}
                                    onClick={handleOpen}
                                    >
                                    Continuar
                                  </MKButton>
                                  <MKButton 
                                    onClick={sendHandler}
                                    type="submit" name="submit" color="error" style={{ variant: 'gradient', color: 'white', marginBottom: '10px' }}
                                    component={Link}
                                    to="/home">
                                    Cancelar
                                  </MKButton>
                                    <Modal
                                      open={open}
                                      onClose={handleClose}
                                      aria-labelledby="modal-modal-title"
                                      aria-describedby="modal-modal-description"
                                    >
                                      
                                    <Box sx={style}>
                                      <MKBox
                                          variant="gradient"
                                          coloredShadow="info"
                                          borderRadius="lg"
                                          p={2}
                                          mx={2}
                                          mt={-3}
                                          style={{ backgroundColor: 'white' }}
                                        >
                                          <Grid container item justifyContent="center" xs={12}>
                                          <MKTypography variant="h3" color="info" mb={1} ml={6}>
                                          Presion para Finalizar 
                                          </MKTypography>
                                          <MKBox display="flex" color="white" p={1}>
                                            <MKTypography
                                              component="span"
                                              variant="button"
                                              color="black"
                                              opacity={0.8}
                                              ml={1}
                                              fontWeight="regular"
                                            >
                                              ESTOS DATOS SER PARA NUESTRO PROPIO USO SI AFECTAR NINGUNA DE LAS DOS PARTES.
                                              Este proceso tardar en un estimado de 24 hr hasta 72 hr para validar los datos.
                                            </MKTypography>
                                          </MKBox>
                                          
                                          <MKButton 
                                            onClick={sendHandler}
                                            type="submit" name="submit" style={{ backgroundColor: '#0E3673', variant: 'gradient', color: 'white', marginBottom: '10px' }}
                                            component={Link}
                                            to="/home">
                                              Registrarse
                                          </MKButton>
                                         </Grid>
                                        </MKBox>
                                    </Box>
                                    </Modal>

                  </Grid>
                </MKBox>
              </MKBox>
            </MKBox>
          </Grid>
        </Grid>
      </form>
    </Fragment>
  );
}

export default RegistroTrabajador;


