import React from 'react';

import 'styles/profile.css';
import User from "pages/LandingPages/Author/sections/User";
import MKBox from 'components/MKBox';
import DefaultNavbar from 'examples/Navbars/DefaultNavbar';
import routes from "routes";

const data = [
  { id: 1, nombre: "Erik Carranza", puesto:"Organizador de Fiestas", descripcion:"Tengo excelentes ideas para los jovenes", direccion:"Maximino Pozo #89",imagen:"https://i.postimg.cc/SNg3V53v/ivana-square.jpg"},
  { id: 2, nombre: "Iraz Lozano", puesto:"Fotografa", descripcion:"Las fotos de bodas y XV aos son mi especialidad", direccion:"Independencia #378",imagen:"https://i.postimg.cc/wM0ffyfm/ivana-squares.jpg"},
];

class PerfilEventos extends React.Component{
  
  constructor(props){
    super(props);

    this.state ={
      data:data
    }
  }

  render(){
    return (
        <>
          <DefaultNavbar
            routes={routes}
          /> 
          <MKBox
            minHeight="25vh"
            width="100%"
            sx={{
              
              backgroundSize: "cover",
              backgroundPosition: "top",
              display: "grid",
              placeItems: "center",
            }}
          />  
            {
            this.state.data.map((elemento)=>(
              <User 
              nombre ={elemento.nombre}
              puesto ={elemento.puesto}
              direccion ={elemento.direccion}
              imagen={elemento.imagen}
              />
            ))
          }
        </>
      );
      }
    }

export default PerfilEventos;